<template>
  <v-container class="fill-height" >
    <!-- <div class="mt-5"> -->
      <v-row align="center" justify="center" class="mt-5" v-if="cardFormLoading">
        <div class="text-center ma-12" >
          <v-progress-circular
            :size="150"
            class="mx-3"
            indeterminate
            color="primary"
          ></v-progress-circular>
        </div>
      </v-row>
      <v-row align="center" justify="center" class="mt-5">
        <v-col cols="12" v-bind:key="key" v-for="(app,key) in apps" lg="4" md="3" sm="12">
          
          <v-card
            v-if="!cardFormLoading"
            class="mx-auto d-flex align-center justify-center pa-3 elevation-12"
            outlined
            :to="app.route"
            :color="app.color"
            dark
            style="height: 200px;"
          >
            <v-list-item three-line>
              <v-list-item-content>
                <div class="headline mb-1 text-center">{{app.title}}</div>
                <v-list-item-subtitle class="text-center mt-4">
                  <v-icon class="mx-2" large>{{app.icon}}</v-icon>
                  <div class="headline mt-5 text--white" light> {{app.count}} </div>
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>
      </v-row>
    <!-- </div> -->
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  computed: {
    ...mapGetters(["cardFormLoading", "eTriageEmployeesCount"]),
  },
  data() {
    return {
      date: new Date().toISOString().substr(0, 10),
      apps: [
        {
          color: '#007bff',
          title: 'Employee / Residents / Consultants',
          route: '/employees',
          icon: 'fas fa-hospital-user',
          count: this.$store.state.eTriageEmployeesCount.length,
        },
        {
          color: '#dc3545',
          title: 'Visitors',
          route: '/visitors',
          icon: 'fas fa-users',
          count: this.$store.state.eTriageVisitors.length,
        },
        {
          color: 'purple',
          title: 'Third Party Contractor',
          route: '/tpc',
          icon: 'fas fa-user-cog',
          count: this.$store.state.eTriageTPC.length,
        },
      ],
    };
  },
  mounted() {
    if (!localStorage.isLoggedIn) {
      this.$router.push("/");
    }
    this.getETriageTodayCount();
  },
  methods: {
    async getETriageTodayCount() {
      await this.$store.dispatch("getETriageEmployees",this.date);
      await this.$store.dispatch("getETriageOtherUsers",this.date);
    }
  }
};
</script>

